html {
	height: 100vh;
	width: 100vw;
	margin: 0;
	padding: 0;
	font-size: 10px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  font-family: "Teko", sans-serif;
}

#root {
	height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100vh;
  width: 100vw;
  /*background-image: url("https://res.cloudinary.com/rezdelamasa/image/upload/v1565801596/ApexLegend/app-background.jpg");*/
  background-color: #CDC9BF;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.Main {
	height: 100vh; width: 100vw;
	/*background-color: #E1DDD2;*/
	box-sizing: border-box;
	position: relative;
	overflow: hidden;
	display: flex;
	flex-direction: column;

	/*box-shadow: 3px 3px 8px #555;*/
}
.category-list {
	position: relative;
	display: flex;
	flex-direction: row;
	justify-content: center;
	list-style-type: none;
	height: 10%;
	width: 100%;
	margin: 0 auto;
	padding: 0;
	/*box-shadow: -5px 0 10px #333;*/
	z-index: 0;
}
.category__card {
	height: 100%;
	/*background-color: red;*/
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	position: relative;
	width: 100%;
	min-width: 300px;
}

.category__card:hover {
	cursor: pointer;
}

.category__card:hover .category__background {
	-webkit-filter: contrast(0.2);
	        filter: contrast(0.2);
	-webkit-filter: brightness(150%);
	        filter: brightness(150%);
}

.category__card:hover .category__heading {
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
	text-shadow: 1px 3px 0 #333, 1px 15px 8px rgba(20,20,20,0.5);
}

.category__card--left, .category__card--right {
	height: 95%;
}

.category__card--main {
	 height: 100%;
	 min-height: 100%;
	 width: 65%;
}

.category__card--top {
	background-color: #972930;
}

.category__card--bottom {
	background-color: #191816;
}


.category__card--middle .category__heading {
	font-size: 10rem;
	text-shadow: 1px 3px 0 #333, 1px 15px 5px rgba(20,20,20,0.5);
}

.category__background {
	width: 100%;
	height: 100%;
	position: absolute;
	z-index: 0;
}

.category__heading {
	color: white;
	margin: 0;
	position: relative;
	z-index: 1;
	font-size: 3rem;
	text-shadow: 1px 3px 0 #333, 1px 13px 5px rgba(20,20,20,0.5);
	letter-spacing: 2px;
}
.Legends {
	width: 100%; height: 100%;

	display: flex;
	flex-direction: rowd;
}

.display-area {
	flex-grow: 1;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	position: relative;
}

.display__background {
	width: auto;
	height: 80vh;
	width: 30vw;
	object-fit: cover;
	position: absolute; 
	top: 0; left: 5%;
}

.display__background-text {
	position: absolute;
	margin: 0;
	margin-top: 50px;
	color: white;
	font-size: 10vw;
	z-index: 2;
	letter-spacing: 0.5vw;
	text-shadow: 5px 5px 10px #333;
}

.legend__avatar {
	display: none;
	max-width: 30%;
	/*width: 35%;*/
	height: auto;
	max-height: 35vw;
	position: relative;
	z-index: 3;
}

[class^="hover--"] {
	display: flex;
}
