.LegendsList {
	height: 100%; width: 10vh;
	min-width: 10vh;
	/*max-width: 16vw;*/
	list-style-type: none;
	display: flex;	
	flex-direction: column;
	/*flex-wrap: wrap;*/
	justify-content: space-between;
	padding: 0;
	margin-top: 0;
	box-shadow: 0 -2px 4px #333;
	position: relative;
	z-index: 10;
}

.Legend {
	width: 100%; height: 10vw;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	box-sizing: border-box; 
	position: relative;
	overflow: hidden;
}

.Legend:hover {
	cursor: pointer;
}

.Legend::after {
	content: "";
	display: block;
	padding-top: 100%;
}

img {
	width: 100%;
	height: 100%;
	/*border: 3px solid white;*/
	box-sizing: border-box;
	position: relative;
	top: 0;
}

.shadow {
	position: absolute;
	height: 100%;
	width: 100%;
	top: 0;
	left: 0;
	box-shadow: inset 0 10px 30px #333;
	z-index: 5;
	/*pointer-events: none;*/
}

.thumbnail-hover {
	opacity: 0;
	position: absolute;
	top: 0;
	left: 0;
	-webkit-transform: scale(1);
	        transform: scale(1);
	-webkit-transition-property: opacity, -webkit-transform;
	transition-property: opacity, -webkit-transform;
	transition-property: opacity, transform;
	transition-property: opacity, transform, -webkit-transform;
	-webkit-transition-duration: 0.4s;
	        transition-duration: 0.4s;
	-webkit-transition-timing-function: ease-in-out;
	        transition-timing-function: ease-in-out;
	/*-webkit-animation: fade-out 0.3s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: fade-out 0.3s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;*/
}

.thumbnail-click {
	opacity: 1;
}

.shadow:hover + .thumbnail-hover {
	visibility: visible;
	opacity: 1;
	-webkit-transition-property: opacity, -webkit-transform;
	transition-property: opacity, -webkit-transform;
	transition-property: opacity, transform;
	transition-property: opacity, transform, -webkit-transform;
	-webkit-transition-duration: 0.4s;
	        transition-duration: 0.4s;
	-webkit-transition-timing-function: ease-in-out;
	        transition-timing-function: ease-in-out;
	-webkit-transform: scale(1.02);
	        transform: scale(1.02);
}

